@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }
input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

* { box-sizing: border-box; }
*:before, *:after { box-sizing: border-box; }

@font-face { font-family: roboto-black; src: url("fonts/roboto-black.eot"); src: url("fonts/roboto-black.eot?#iefix") format("embedded-opentype"), url("fonts/roboto-black.woff") format("woff"), url("fonts/roboto-black.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.title_mod span:before, .nav:after, .footer:before, .section:after { background-image: linear-gradient(to right, #734c33 0%, #876242 20%, #e2c98f 50%, #a3875e 68%, #816344 70%, #634026 85%, #f5c955 100%); }

.nav:before { background-image: linear-gradient(to right, rgba(115, 76, 51, 0.5) 0%, rgba(135, 98, 66, 0.5) 20%, rgba(226, 201, 143, 0.5) 50%, rgba(163, 135, 94, 0.5) 68%, rgba(129, 99, 68, 0.5) 70%, rgba(99, 64, 38, 0.5) 85%, rgba(245, 201, 85, 0.5) 100%); }

html { font-size: 62.5%; }
@media only screen and (max-width: 1050px) { html { font-size: 56%; } }

body { font-family: "Calibri Light", "Helvetica", sans-serif; font-size: 2rem; line-height: 1.4; }

a { text-decoration: none; }

.container { max-width: 1200px; padding: 0 3rem; margin: 0 auto; *zoom: 1; }
.container:after { content: " "; display: table; clear: both; }

.logo { font: 0/0 a; text-shadow: none; color: transparent; width: 175px; height: 45px; display: inline-block; }

.title-wrap { position: relative; z-index: 1; overflow: hidden; }

.title { color: #b3810a; font-size: 3.6rem; text-transform: uppercase; position: relative; display: inline-block; padding-bottom: 3px; margin: 2rem 0; z-index: 2; }
.title:after { content: ""; position: absolute; bottom: 0; left: 48%; margin-left: -4rem; width: 8rem; height: 2px; background: #B3810A; }
.title_left:after { left: 0; margin-left: 0; }
.title_mod span { background: #fff; display: inline-block; position: relative; padding: 0 3rem; margin-left: -3rem; }
.title_mod span:before { content: ""; position: absolute; top: 50%; margin-top: -2px; height: 4px; left: -1000px; right: -2000px; z-index: -2; }
.title_noline:after { display: none; }

.title-md { position: relative; color: #b3810a; font-size: 2.4rem; font-weight: 100; margin-bottom: 2rem; }
.title-md span { font-size: 3.6rem; }
.title-md:after { content: ""; width: 8rem; height: 2px; position: absolute; left: 0; bottom: 0; background: #b3810a; }

.title-sm { font-size: 2rem; font-weight: bold; color: #4a2005; margin: 4rem 0; font-family: "Calibri", sans-serif; }

.btn { padding: 1.4rem 3.8rem 1.1rem; border-radius: 5px; font-family: "roboto-black", Arial, sans-serif; font-size: 1.4rem; line-height: 1; display: inline-block; border: 1px solid #cebe93; background: linear-gradient(to top, #c6bb9e 0%, #eee2c0 100%); text-shadow: 0px 1px 0px rgba(255, 238, 186, 0.73); color: #857f6e; text-transform: uppercase; box-shadow: 0px 2px 3px 0px #716e67, inset 0px 1px 0px 0px #faf2d2, inset 1px 0px 0px 0px #faf2d2; transition: background 0.25s; }
.btn:hover { background: linear-gradient(to top, #bdb08e 0%, #e9d9ac 100%); }
.btn_lg { font-size: 1.8rem; padding: 1.6rem 2rem 1.4rem; text-align: center; }
.btn_yellow { border: 1px solid #ffed8c; border-right-color: transparent; border-bottom-color: transparent; border-radius: 3px; background: linear-gradient(to top, #ddb23e 0%, #f4c64c 100%); box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.8); color: #4c2105; text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.3); }
.btn_yellow:hover { background: linear-gradient(to top, #d9a928 0%, #f3be34 100%); }
.btn_yellow:active { background: linear-gradient(to top, #ddb23e 0%, #f4c64c 100%); }

.btn-action { position: relative; padding: 1.4rem 1.3rem 0.8rem; border: 1px solid #f6e6b8; font-family: "roboto-black", Arial, sans-serif; color: #98917e; text-shadow: 0px 1px 0px rgba(255, 238, 186, 0.73); display: inline-block; font-size: 1.4rem; border-radius: 5px; background: linear-gradient(to top, #c7bc9f 0%, #eee1bf 100%); box-shadow: 0px 2px 3px 0px #716e67, inset 0px 1px 0px 0px #fffdde, inset 1px 0px 0px 0px #fffdde; line-height: 1; transition: background 0.3s; }
.btn-action i { display: inline-block; vertical-align: middle; margin-right: 1.5rem; }
.btn-action:hover { background: linear-gradient(to top, #beb18f 0%, #e9d8ab 100%); }

.page-title { font-size: 7.8rem; color: #6b482a; text-align: center; line-height: 1; text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.5); }

.popup { display: none; position: absolute; left: 0; right: 0; top: 8rem; z-index: 99; }
@media only screen and (max-width: 767px) { .popup { padding: 0 10px; } }
.popup__in { margin: 0 auto; font-size: 2rem; max-width: 360px; border-radius: 5px; background: linear-gradient(to top, #c7bc9f 0%, #eee1bf 100%); box-shadow: 0px 2px 3px 0px #716e67, inset 0px 1px 0px 0px #fffdde, inset 1px 0px 0px 0px #fffdde; border: 1px solid #f6e6b8; padding: 2rem; }
.popup__header { font-family: "Calibri"; font-weight: bold; color: #98917e; text-shadow: 0px 1px 0px rgba(255, 238, 186, 0.73); font-size: 3rem; text-align: center; text-transform: uppercase; margin-bottom: 1.6rem; }
.popup .field { color: #4a2005; *zoom: 1; }
.popup .field:after { content: " "; display: table; clear: both; }
.popup .field label { float: left; width: 8rem; margin-right: 10px; }
.popup .field input { border-radius: 5px; margin-bottom: 9px; }
.popup .field__input { overflow: hidden; }
.popup .field__input input { width: 100%; height: 3.6rem; padding: 1rem; }
.popup .field label, .popup .field input { display: inline-block; vertical-align: middle; }
.popup .field_checkbox label { width: 100%; }
.popup__formbuttton { text-align: center; margin-top: 2rem; }

.checkbox { position: relative; cursor: pointer; line-height: 1; }
.checkbox__input { position: absolute; top: 0; left: 0; opacity: 0; z-index: 2; }
.checkbox__text { width: 100%; font-size: 13px; cursor: pointer; font-family: "Calibri"; }
.checkbox__text i { display: inline-block; vertical-align: middle; margin-right: 4px; width: 12px; height: 12px; border-radius: 2px; background: #fff; position: relative; top: -1px; }
.checkbox .checkbox__input:checked + .checkbox__text i:before { content: ""; position: absolute; width: 10px; height: 10px; top: 1px; left: 1px; background-image: url(../img/check.svg); background-size: 10px 10px; background-repeat: no-repeat; display: block; }

.m-nav { display: none; padding: 1rem 0; *zoom: 1; }
.m-nav:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 767px) { .m-nav { display: block; } }
.m-nav__text { color: #ffcd48; font-size: 2rem; float: left; font-weight: bold; line-height: 1; padding-top: 3px; }
.m-nav .humb { float: right; }

.humb { height: 25px; width: 25px; position: relative; cursor: pointer; }
.humb span { background: #ffcd48; height: 2px; display: block; cursor: pointer; position: absolute; top: 50%; margin-top: -1px; left: 0; right: 0; transition: transform 0.25s, background 0.2s; }
.humb span:before, .humb span:after { content: ""; position: absolute; left: 0; right: 0; height: 2px; background: #ffcd48; transition: transform 0.25s, background 0.2s; }
.humb span:before { top: -7px; }
.humb span:after { bottom: -7px; }
.humb.is-active span { background: transparent; }
.humb.is-active span:before { transform: rotate(45deg); top: 1px; height: 2px; }
.humb.is-active span:after { transform: rotate(-45deg); bottom: -1px; height: 2px; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

[class^="i-"], [class*=" i-"] { display: inline-block; }

.i-phone { background-image: url("../img/icons.png"); width: 18px; height: 22px; background-position: 0px -51px; }

.l { *zoom: 1; }
.l:after { content: " "; display: table; clear: both; }
.l .l-col1 { float: left; width: 48%; }
.l .l-col2 { float: right; width: 48%; }

.header { background-size: cover; background-position: 50% 100%; background-repeat: no-repeat; padding-bottom: 4rem; }
.header__top { *zoom: 1; }
.header__top:after { content: " "; display: table; clear: both; }
.header__top .logo { float: left; margin-top: 3rem; }
.header__top .btn-action { float: right; margin-top: 3.5rem; }
@media only screen and (max-width: 767px) { .header__top .logo, .header__top .btn-action { float: none; display: block; margin: 2rem auto; }
  .header__top .btn-action { margin-top: 4rem; } }
.header .page-title { margin-top: 3rem; }
.header .nav { margin-top: 13.5rem; }
.header__button { text-align: center; margin-top: 2.8rem; }

.nav { background: #936645; position: relative; }
.nav:before { content: ""; position: absolute; bottom: 100%; left: 0; right: 0; height: 3px; }
.nav:after { content: ""; position: absolute; left: 0; right: 0; bottom: 0; height: 3px; }
.nav__list { position: relative; text-align: left; line-height: 1; margin: 0; font-size: 0; display: block; }
@media only screen and (max-width: 1180px) { .nav__list { text-align: center; } }
@media only screen and (max-width: 767px) { .nav__list { display: none; position: absolute; background: #936645; width: auto; z-index: 4; left: 0; right: 0; } }
.nav__list li { font-size: 1.8rem; line-height: 1; padding: 2rem 0; display: inline-block; vertical-align: top; }
@media only screen and (max-width: 767px) { .nav__list li { width: 100%; } }
.nav__list a { color: #fff; font-size: 1.8rem; padding: 0 10px; text-transform: uppercase; transition: color 0.3s; }
.nav__list a:hover, .nav__list a.is-active { color: #ffcd48; }
.nav__list a.is-active { font-weight: bold; }

.footer { background: #B3810A; padding-top: 4.3rem; padding-bottom: 6.5rem; position: relative; }
.footer:before { content: ""; position: absolute; left: 0; right: 0; top: 0; height: 3px; }
.footer__item { float: left; }
.footer__item p { color: #fff; font-size: 1.6rem; }
.footer__item p a { color: #fff; font-weight: bold; }
.footer__item:first-child { width: 22%; }
.footer__item:last-child { float: right; }
@media only screen and (max-width: 930px) { .footer__item { float: none; width: 100%; margin-bottom: 10px; } }

.section { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-top: 3rem; height: 56rem; position: relative; }
@media only screen and (max-width: 900px) { .section { height: auto; padding-bottom: 4rem; } }
.section:after { content: ""; position: absolute; height: 3px; left: 0; right: 0; bottom: 0; }
.section:last-of-type:after { display: none; }
.section .title { margin-bottom: 3rem; }
.section__bl { width: 50%; text-align: center; float: left; }
.section__bl.is-right { float: right; }
@media only screen and (max-width: 767px) { .section__bl { width: 100%; } }
.section__text { color: #4a2005; font-size: 2.4rem; line-height: 1; }
.section__textheader { font-weight: bold; margin-bottom: 1rem; font-family: "Calibri", sans-serif; }
.section__text.is-light { color: #fff; }
@media only screen and (max-width: 900px) { .section__text { font-size: 2rem; } }
.section__btn { margin-top: 2rem; }

.article { margin: 0 -2.5rem; font-size: 0; *zoom: 1; }
.article:after { content: " "; display: table; clear: both; }
.article__col { display: inline-block; width: 50%; padding: 0 2.5rem; font-size: 1.4rem; vertical-align: top; margin-bottom: 3rem; }
@media only screen and (max-width: 767px) { .article__col { width: 100%; } }
.article__col1 { width: 100%; padding: 0 2.5rem; }
.article p { color: #4a2005; font-size: 2rem; line-height: 1.1; }
.article ul { list-style-type: disc; font-size: 2rem; margin-left: 2rem; }
.article ul li { color: #b3810a; }
.article ul li p { color: #4a2005; }

.cite { background: #F5E8C8; padding: 5rem 0; margin-top: 3rem; }
.cite__text { text-align: center; color: #4a2005; margin: 0 auto; }
.cite__text_sm { max-width: 800px; }
.cite__text p { font-size: 2rem; }
.cite__text .btn { margin-top: 3rem; }
.cite__header { font-size: 3rem; margin-bottom: 1rem; }
.cite__header.title { margin-top: 0; font-size: 3.6rem; }

.man .container { position: relative; *zoom: 1; }
.man .container:after { content: " "; display: table; clear: both; }
.man__img, .man__text { width: 50%; }
.man__img { position: absolute; left: 0; top: -1.5rem; bottom: 0; text-align: center; }
.man__text { color: #4a2005; font-size: 2rem; float: right; padding: 11rem 0 16.5rem; line-height: 1.2; font-weight: 100; }
.man__text strong { font-weight: bold; }
@media only screen and (max-width: 660px) { .man__text { width: 100%; float: none; position: relative; z-index: 2; padding: 18rem 0 4rem; } }

.accordion-wrap { margin: 4rem 0 6rem; }

.accordion { position: relative; }
.accordion .cite { margin-top: 0; }
.accordion .container { position: relative; }
.accordion__body { display: none; overflow: hidden; }
.accordion__header { text-align: center; position: absolute; left: 0; right: 0; top: 0; }
.accordion__header:before { content: ""; position: absolute; top: 0; left: 50%; margin-left: -38rem; width: 0px; height: 0px; border-style: solid; border-width: 16rem 38.75rem 0 38.75rem; border-color: #F5E8C8 transparent transparent transparent; }
.accordion__text { padding-top: 20rem; }
.accordion:first-of-type { margin-top: 4rem; }
.accordion:last-of-type { margin-bottom: 6rem; }
.accordion:nth-of-type(2n) .cite { background: #fff8d7; }
.accordion:nth-of-type(2n) .accordion__header:before { border-top-color: #fff8d7; }


